.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.texto_centrado {
  text-align: center;
}

.texto_justificado {
  text-align: justify;
}

.texto_derecha {
  text-align: right;
}

.texto_izquierda {
  text-align: left;
}

.mayuscula {
  text-transform: capitalize;
}

.all-mayuscula {
  text-transform: uppercase;
}

.no-visible {
  display: none;
}

/** Media Queries **/
body {
  /* background-color: white; */
  flex-direction: column;
  place-items: normal;
}

#app {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  margin: 0 !important;
  padding: 0 !important;
  padding-bottom: 2rem !important;
}

.trabajo {
  display: flex;
  flex-direction: column;
}

h1, h2 {
  /* color: green; */
}

button {
  padding: 1rem 1rem;
}

ul li {
  color: black;
}

ul {
  list-style-type: decimal;
}

.fruta {
  margin-bottom: 2rem;
}

.Mouse {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  margin-top: 2rem;
}
.Mouse .subtotal {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.Mouse .Calcu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
.Mouse h3 {
  display: flex;
  justify-content: center;
  color: brown;
}
.Mouse .reset {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.Post {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
}
.Post .botones {
  display: flex;
  flex-direction: row;
  grid-gap: 0.7rem;
}
.Post .boton-Post {
  margin: auto;
  width: 100%;
}
.Post .boton-blo {
  background-color: rgb(229, 229, 229) !important;
  pointer-events: none;
}

.caja-1 {
  margin: auto;
}
.caja-1 .caja-body {
  display: flex;
  flex-direction: column;
  border: 2px solid red;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
}
.caja-1 .card-title {
  margin: 0 auto 1rem;
  font-family: serif;
  font-size: 1rem;
  color: brown;
}
.caja-1 p {
  font-family: "Times New Roman", Verdana, serif;
  font-size: 0.7rem;
  margin: 0 auto;
}
.caja-1 button {
  margin: 1rem auto 0.5rem;
  border-radius: 10rem;
  padding: 0.8rem;
  font-size: 0.7rem;
  background-color: white;
  font-weight: bold;
}
.caja-1 button:hover {
  background-color: #37C0FF;
  color: white;
}

.prueba {
  padding: 0.5rem;
}
.prueba .lista {
  display: flex;
  flex-direction: row;
  grid-gap: 0.8rem;
  margin: auto;
  height: 70%;
  transform: scale(1);
  width: 90%;
  overflow-x: scroll;
  margin-bottom: 1rem;
  /* position: fixed; */
}

nav a {
  color: #6E75FF;
}

.head-register header {
  /* background-color: #230023 !important; */
  background-color: #3A003A !important;
  padding-inline: 30px !important;
}
.head-register ul {
  display: flex;
  justify-content: space-evenly;
  background-color: #3A003A;
  line-height: 61px !important;
}
.head-register ul .ant-menu-item a {
  color: white;
}
