  background-color: white;
  color: #242424;
}
.Register .antDesign:hover span, .Login .antDesign:hover span, .Home .antDesign:hover span, .Editar .antDesign:hover span {
  font-weight: bold;
}
.Register .antDesign .anticon, .Login .antDesign .anticon, .Home .antDesign .anticon, .Editar .antDesign .anticon {
  height: 1rem;
}
.Register .BotonEditar, .Login .BotonEditar, .Home .BotonEditar, .Editar .BotonEditar {
  margin: 0 auto;
}
.Register .Userli, .Login .Userli, .Home .Userli, .Editar .Userli {
  padding: 0;
}
.Register .Userli li, .Login .Userli li, .Home .Userli li, .Editar .Userli li {
  display: flex;
  flex-direction: column;
  margin: 0.5rem auto;
  min-width: 12rem;
  border: white 2px solid;
  border-radius: 0.7rem;
  padding: 0.5rem;
}
.Register .Userli li span, .Login .Userli li span, .Home .Userli li span, .Editar .Userli li span {
  color: white;
  margin: 0.2rem auto;
  font-size: 0.8, rem;
  display: inline-block;
  max-width: 15rem; /* Ajusta este valor según sea necesario */
  overflow: hidden;
